.books-section {
  border-bottom: 1px solid;
  justify-content: center;
  padding: 15px 40px;
  display: flex;
}

.booksSection {
  flex-direction: row;
  max-width: 1440px;
  margin: 0 auto;
  display: flex;
}

.books-category {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  gap: 30px;
  display: flex;
}

.books-row {
  flex-direction: column;
  display: flex;
}

.books-section .book {
  width: 200px;
  height: 200px;
}

.books-section-content {
  width: 80%;
  margin-bottom: 10px;
}

.books-section-content p {
  color: #000000a1;
  width: 80%;
  margin-top: 5px;
  margin-bottom: 15px;
  font-size: 1.6em;
}

.books-section h3 {
  font-size: 2em;
}

.books-section-content h2 {
  text-align: left;
  width: 100%;
  font-size: 1.8em;
}

#hardCover:after {
  color: #0ff;
}

.stephen-king-section {
  border-bottom: 1px solid #000;
  border-left: 1px solid #000;
}

.stephen-king-section-content {
  flex-direction: column;
  place-items: center;
  padding: 15px;
  display: flex;
}

.stephen-king-section-content p {
  text-align: center;
  color: #000000a1;
  width: 70%;
  margin-bottom: 15px;
  font-size: 1.6em;
}

.stephen-king-section-content h2 {
  text-align: center;
  margin-bottom: 15px;
  font-size: 1.8em;
}

.book-title {
  text-align: center;
  margin-bottom: 30px;
  font-size: 2em;
}

#read-more {
  cursor: pointer;
  border: 1px solid #000;
  border-radius: 8px;
  width: 200px;
  height: 40px;
  font-size: 1.4em;
  transition: all .5s;
}

#read-more:hover .bx-right-arrow-alt {
  translate: 50%;
}

@media (width <= 768px) {
  .books-section-content p {
    width: 80%;
    font-size: 1.6em;
  }

  #read-more {
    width: 100px;
    height: 25px;
    font-size: .7em;
  }
}

@media screen and (width <= 430px) {
  .booksSection {
    flex-direction: column;
    margin: 0;
    display: flex;
  }

  .books-section {
    margin-bottom: 15px;
    padding: 0;
  }

  .books-section h3, .books-section-content h2 {
    font-size: 1.6em;
  }

  .books-section-content p {
    color: #000000a1;
    width: 80%;
    font-size: 1.2em;
  }

  .book-title {
    margin-bottom: 15px;
  }

  .hardcover-fictions {
    border-right: none;
  }

  .stephen-king-section-content h2 {
    font-size: 1.6em;
  }

  .stephen-king-section-content p {
    font-size: 1.2em;
  }

  .stephen-king-section {
    border-left: none;
  }

  .books-section .book {
    margin: 10px;
  }
}
