.books-section{
    display: flex;
    padding: 15px 40px;
    border-bottom: 1px solid;
    justify-content: center;
}

.booksSection{
    margin: 15px 0;
    display: flex;
    flex-direction: row;
    max-width: 1440px;
    margin: 0 auto;
}

.books-category{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 30px;
}

.books-row{
    display: flex;
    flex-direction: column;
}

.books-section .book{
    width: 200px;
    height: 200px;
}

.books-section-content {
    width: 80%;
    margin-bottom: 10px;
}

.books-section-content p{
    width: 80%;
    margin-top: 5px;
    margin-bottom: 15px;
    font-size: 1.6em;
    color: rgba(0, 0, 0, 0.63);
}

.books-section h3{
    font-size: 2em;
}

.books-section-content h2{
    font-size: 1.8em;
    width: 100%;
    text-align: left;
}

#hardCover::after{
    color: aqua;
}

.stephen-king-section{
    border-left: 1px solid black;
    border-bottom: 1px solid black;
}

.stephen-king-section-content {
    display: flex;
    align-items: center;
    justify-items: center;
    flex-direction: column;
    padding: 15px;
}

.stephen-king-section-content p{
    text-align: center;
    width: 70%;
    margin-bottom: 15px;
    font-size: 1.6em;
    color: rgba(0, 0, 0, 0.63);
}

.stephen-king-section-content h2{
    margin-bottom: 15px;
    text-align: center;
    font-size: 1.8em;
}

.book-title{
    text-align: center;
    font-size: 2em;
    margin-bottom: 30px;
}

#read-more{
    width: 200px;
    height: 40px;
    border-radius: 8px;
    border: 1px solid black;
    cursor: pointer;
    font-size: 1.4em;
    transition: all 0.5s;
}


#read-more:hover .bx-right-arrow-alt{
    translate: 50%;
}


@media (max-width: 768px){
    .books-section-content p{
        font-size: 1.6em;
        width: 80%;
    }

    #read-more{
        width: 100px;
        height: 25px;
        font-size: .7em;
    }
}

@media screen and (max-width: 430px){
    .booksSection{
        display: flex;
        flex-direction: column;
        margin: 0;
    }
    .books-section{
        padding: 0;
        margin-bottom: 15px;
    }

    .books-section h3{
        font-size: 1.6em;
    }

    .books-section-content h2{
        font-size: 1.6em;
    }

    .books-section-content p{
        font-size: 1.2em;
        width: 80%;
        color: rgba(0, 0, 0, 0.63);;
    }

    .book-title{
        margin-bottom: 15px;
    }

    .hardcover-fictions{
        border-right: none;
    }

    .stephen-king-section-content h2{
        font-size: 1.6em;
    }

    .stephen-king-section-content p{
        font-size: 1.2em;
    }

    .stephen-king-section{
        border-left: none;
    }

    .books-section .book{
        margin: 10px;
    }
}